/*--------------------cacao----------------------*/
.cacao {
	padding-bottom: rem(405);

	@include max-screen($lgg){
		padding-bottom: rem(156.95);
	}
}

.cacao__wrapper {}
.cacao__title {
	font-size: rem(22);
	margin-bottom: rem(54);

	@include max-screen($lgg){
		margin-bottom: rem(38);
		font-size: rem(22);
	}
	@include max-screen($md){
		margin-bottom: rem(24);
		font-size: rem(15);
	}
}
.tiny-carousel {}
.cacao__carousel {
	position: relative;
	margin-left: prc(100, $maxWidthContainer);
	margin-right: prc(100, $maxWidthContainer);

	@include max-screen($lgg){
		margin-left: prc(145, 1024);
		margin-right: prc(145, 1024);
	}
	@include max-screen($md){
		margin-left: 0;
		margin-right: 0;
		margin-left: rem(-30);
		margin-right: rem(-30);
	}

	.tiny-carousel__nav {
		@include max-screen($md){
			display: none;
		}
	}

	.tns-nav{
		margin-top: rem(68);

		@include max-screen($lgg){
			margin-top: rem(41);
		}
		@include max-screen($md){
			margin-top: rem(31);
		}
	}
}

.tns-prev,
.tns-next {
	position: absolute;
	top: calc(50% - rem(15 + 68));
	width: rem(30);
	height: rem(30);
	display: flex;
	align-items: center;
	justify-content: flex-end;
	@include but-null();

	@include max-screen($lgg){
		top: calc(50% - rem(15 + 40));
	}
	@include max-screen($md){
		top: calc(50% - rem(15 + 31));
	}
}
.tns-prev{
	left: prc(-150, $maxWidthContainer);
}
.tns-next {
	right: prc(-130, $maxWidthContainer);

}
.tiny-sldr {}
.cacao__slider {}
.cacao__slide {
	aspect-ratio: 965.33 / 724;

	@include max-screen($lgg){
		aspect-ratio: 700 / 545;
	}
	@include max-screen($md){
		aspect-ratio: 360 / 282;
	}
}
._fit-img {}
/*--------------------END cacao----------------------*/ 